import classnames from 'classnames';
import { InspectorControls, InnerBlocks } from '@wordpress/block-editor';
import { PanelBody, ToggleControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import { withSelect } from '@wordpress/data';
import { compose } from '@wordpress/compose';

const conditionalGroupEdit = ( {
	attributes,
	setAttributes,
	className,
	hasInnerBlocks,
} ) => {
	const { hideSp, hideMd, hideLg, hideAMP, onlyAMP } = attributes;
	const classes = classnames( className, 'ystdb-conditional-group', {} );
	const innerClasses = classnames(
		'wp-block-group__inner-container',
		'ystdb-conditional-group__inner',
		{}
	);

	return (
		<>
			<InspectorControls>
				<PanelBody
					title={ __( '', 'ystandard-blocks' ) }
					initialOpen={ true }
				>
					<div className={ `ystdb-info__label` }>
						
					</div>
					<div style={ { marginTop: '1em' } }>
						<ToggleControl
							label={ __(
								'',
								'ystandard-blocks'
							) }
							checked={ hideSp }
							onChange={ ( value ) => {
								setAttributes( { hideSp: value } );
							} }
						/>

						<ToggleControl
							label={ __(
								'',
								'ystandard-blocks'
							) }
							checked={ hideMd }
							onChange={ ( value ) => {
								setAttributes( { hideMd: value } );
							} }
						/>

						<ToggleControl
							label={ __(
								'PC',
								'ystandard-blocks'
							) }
							checked={ hideLg }
							onChange={ ( value ) => {
								setAttributes( { hideLg: value } );
							} }
						/>
					</div>
				</PanelBody>
				<PanelBody
					title={ __( 'AMP  ', 'ystandard-blocks' ) }
					initialOpen={ true }
				>
					<div className={ `ystdb-info__label` }>
						AMP
					</div>
					<div style={ { marginTop: '1em' } }>
						<ToggleControl
							label={ __(
								'AMP',
								'ystandard-blocks'
							) }
							checked={ hideAMP }
							onChange={ ( value ) => {
								setAttributes( { hideAMP: value } );
							} }
						/>
						<ToggleControl
							label={ __(
								'AMP',
								'ystandard-blocks'
							) }
							checked={ onlyAMP }
							onChange={ ( value ) => {
								setAttributes( { onlyAMP: value } );
							} }
						/>
					</div>
				</PanelBody>
			</InspectorControls>

			<div className={ classes }>
				<div className={ innerClasses }>
					<InnerBlocks
						renderAppender={
							hasInnerBlocks
								? undefined
								: () => <InnerBlocks.ButtonBlockAppender />
						}
					/>
				</div>
			</div>
		</>
	);
};

export default compose( [
	withSelect( ( select, { clientId } ) => {
		const { getBlock } = select( 'core/block-editor' );

		const block = getBlock( clientId );

		return {
			hasInnerBlocks: !! ( block && block.innerBlocks.length ),
		};
	} ),
] )( conditionalGroupEdit );
